import { BrowserRouter as Router, Route } from "react-router-dom";
import Editor from "./components/Editor";

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Route exact path="/">
        <Editor />
      </Route>
      <Route path="/:id">
        <Editor />
      </Route>
    </Router>
  );
}

export default App;
